import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Link, Switch, FormLayout, Textarea,  Div, CellButton,  Button, platform, IOS, Group, Cell, List} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24BrowserForward from '@vkontakte/icons/dist/24/browser_forward';
import Icon24DoNotDisturb from '@vkontakte/icons/dist/24/do_not_disturb';
import Icon24Newsfeed from '@vkontakte/icons/dist/24/newsfeed';
import Icon24User from '@vkontakte/icons/dist/24/user';
import Icon24Favorite from '@vkontakte/icons/dist/24/favorite';

import Icon24Recent from '@vkontakte/icons/dist/24/recent';
const osname = platform();

const Pet = ({ go, id, handleTransferChange, nickname, handleNickChange, handleNickSubmit, userAcc }) => (
   <Panel id={id}>
             <PanelHeader
      left={<HeaderButton onClick={() => window.history.back()}>
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
      </HeaderButton>}

      >
      Блинные булочки
      </PanelHeader>

<Group>
<List>
<Cell before={<Icon24User fill="#4bb34b" />} indicator={<Link component = "a" href = "https://www.spletenie.ru/users/njutta/povarenok/">нютта</Link>}>Автор  </Cell>
<Cell before={<Icon24Newsfeed fill="#4bb34b" />} indicator={<Link component = "a" href = "https://www.povarenok.ru">povarenok.ru</Link>}>Ресурс </Cell>

</List>
</Group> 

  <Group>
              <List>
                <Cell before={<Icon24Recent fill="#4bb34b" />} indicator="1 час">Время приготовления </Cell>
             </List>
            </Group>
     



<Group>
<Div>Ингредиенты:
<br />• Яйцо куриное — 2 шт
<br />• Молоко — 230 мл
<br />• Мука пшеничная — 120 г
<br />• Соль — 0,5 ч. л.</Div>
</Group>

         <Group>
<Div>Рецепт: 
<br />• Взбить немного яйца, добавить муку и сахар, все это тщательно перемешать. 
<br />• Понемножку доливать молоко, что бы добиться однородной массы, сильно не взбивать. 
<br />• Тесто разлить до половины по формам для маффинов. 
<br />• Духовку разогреть до 230 градусов и поставить формочку на 15 минут, затем температуру снизить до 180 градусов и выпекать ещё 13-20 минут.

</Div>
</Group>
  
<Group>
              <List>
                <Cell before={<Icon24Favorite fill="#ffa500"/>} indicator="3.9">Рейтинг </Cell>
             </List>
            </Group>
	</Panel>
);

Pet.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Pet;
